/* hind-regular - latin */
@font-face {
  font-family: "Hind";
  font-style: normal;
  font-weight: 400;
  src: local("Hind Regular"), local("Hind-Regular"),
    url("../fonts/hind/hind-v10-latin-regular.woff2") format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url("../fonts/hind/hind-v10-latin-regular.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
